<template>
    <div>
        <div class="rating_page">
            <header id="head_top1">
                <section class="head_goback1" @click="goBack">
                    <i class="el-icon-arrow-left"></i>
                </section>
                <section class="title_head1 ellipsis">
                <span class="title_text1">
                    选择地址
                </span>
                </section>
            </header>
            <section class="scroll_container">
                <section class="list_cotainer">
                    <ul class="deliverable_address">
                        <li v-for="(v,k) in address" :key="v.name" @click="chooseAddress(v,k)">
                            <i class="el-icon-success" :class="{bgc:k==current}"></i>
                            <div>
                                <header class="">
                                    <span>{{v.name}} </span>
                                    <span> {{v.sex==1?'先生':'女士'}}</span>
                                    <span> {{v.phone}}</span>
                                </header>
                                <div class="address_detail">
                                    <span>{{v.tag}}</span>
                                    <p class="">{{v.address_detail}}</p>
                                </div>
                            </div>
                        </li>
                    </ul>
                </section>
            </section>
            <router-link to="/addAddress">
                <i class="el-icon-circle-plus-outline"></i>
                <span>新增收货地址</span>
            </router-link>
        </div>
    </div>
</template>

<script>
    export default {
        name: "ChooseAddress",
        data(){
            return{
                user:[],
                address:[],
                current:0,
                shop:"",
            }
        },
        created(){
            this.shop = this.$store.state.shop
            console.log(this.shop);
            if (localStorage.getItem('user')){
                this.user = JSON.parse(localStorage.getItem('user'));
                console.log(this.user);
                this.axios.get("v1/users/"+this.user[0].user_id+"/addresses").then(res=>{
                    console.log(res);
                    this.address = res
                }).catch(err=>{

                });
            }

        },
        methods:{
            goBack(){
                this.$router.go(-1)
            },
            chooseAddress(v,k){
                this.current = k;
                console.log(v);
                this.$router.push({
                    path:"/confirmOrder",
                    query:{
                        address:v,
                        index:k,
                        shop:this.shop,
                    }
                })

            }
        }
    }
</script>

<style scoped>
    #head_top1{
        background-color: #3190e8;
        position: fixed;
        z-index: 100;
        left: 0;
        top: 0;
        width: 100%;
        height: 1.95rem;
    }
    .head_goback1{
        left: .4rem;
        width: .6rem;
        height: 1rem;
        line-height: 2.2rem;
        margin-left: .4rem;
        color: #fff;
    }
    .title_head1{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 50%;
        color: #fff;
        text-align: center;
    }
    .title_text1 {
        font-size: .8rem;
        color: #fff;
        text-align: center;
        font-weight: 700;
    }
    .rating_page{
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #fff;
        z-index: 202;
        padding-top: 1.95rem;
    }
    .rating_page>a{
        position: fixed;
        bottom: 0;
        left: 0;
        right: 0;
        height: 2.5rem;
        background-color: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 204;
        text-decoration: none;
    }
    .rating_page>a>i{
        height: 24px;
        width: 24px;
        color: #3190e8;
    }
    .rating_page>a>span {
        font-size: .7rem;
        color: #3190e8;
        margin-left: .3rem;
    }
    .scroll_container {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        padding-top: 1.95rem;
        overflow-y: auto;
    }
    .list_cotainer {
        padding-bottom: 5rem;
    }
    .deliverable_address{
        background-color: #fff;
    }
    .deliverable_address li{
        display: flex;
        align-items: center;
        border-bottom: .025rem solid #f5f5f5;
        padding: .7rem;
        line-height: 1rem;
    }
    .deliverable_address li i {
        /*opacity: 1;*/
    }
    .deliverable_address li i {
        font-size: .8rem;
        color: #f5f5f5;
        margin-right: .4rem;
        /*opacity: 0;*/
    }
    .deliverable_address li header{
        font-size: .7rem;
        color: #333;
    }
    .deliverable_address li header span:first-of-type {
        font-size: .8rem;
        font-weight: 700;
    }
    .rating_page p, .rating_page span {
        font-family: Helvetica Neue,Tahoma,Arial;
    }
    .deliverable_address li .address_detail {
        width: 100%;
        display: flex;
        align-items: center;
    }
    .deliverable_address li .address_detail span {
        font-size: .5rem;
        color: #fff;
        border-radius: .15rem;
        background-color: #ff5722;
        height: .6rem;
        line-height: .6rem;
        padding: 0 .2rem;
        margin-right: .3rem;
    }
    .deliverable_address li .address_detail p{
        font-size: .6rem;
        color: #777;
    }
    .bgc{
        color: #4cd964;
    }
</style>